









import Vue from 'vue';

const variants : string[] = ['primary', 'secondary', 'darkred', 'lightgrey', 'rose'];

export default Vue.extend({
    props: {
        icon: String,
        disabled: Boolean,
        variant: {
            type: String,
            default: 'secondary',
            validator: (value: string) => variants.indexOf(value) !== -1,
        }
    },
    computed: {
        isIconSet(): boolean {
            return this.icon != null && this.icon !== '';
        }
    }
});
