


































import Vue from 'vue';

const allowedTypes = [
    'text',
    'email',
    'number',
    'textarea',
    'password',
    'url'
];

export interface IError {
    active: boolean,
    message: string,
}

export default Vue.extend({
    props: {
        id: {
            type: String,
            required: true,
        },
        label: String,
        description: String,
        required: Boolean,
        placeholder: String,
        rows: Number,
        type: {
            type: String,
            validator: (value: string) => allowedTypes.indexOf(value) !== -1,
            default: 'text'
        },
        value: [String, Number],
    },
    computed: {
        groupId(): string {
            return `group-${this.id}`;
        },
        errorId(): string {
            return `error-${this.id}`;
        },
    },
});
